import { Component, OnInit } from '@angular/core';
//Router
import { Router, ActivatedRoute } from '@angular/router';
//RXJS
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Model Address
import { Address } from '../../../../../partials/layout/tacgui/_forms/obj-addresses-form/obj-address.model'
//Service//
import { AddressesService } from '../addresses.service'

@Component({
  selector: 'kt-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

  validation = new BehaviorSubject({})
  loadingForm = new BehaviorSubject(false)

  formParameters = {
    action: 'add',
    data: new Address,
    old_data: new Address
  }

  constructor(
    private toastr:ToastrService,
    private addr_service: AddressesService,
    private router:Router,
    private route: ActivatedRoute
  ) {

  }

  ngOnInit() {
    this.formParameters.data.empty()
    this.formParameters.old_data.empty()
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  add(){
    this.loadingForm.next(true)
    //console.log(this.formParameters.data)
    let data = this.makeClone(this.formParameters.data)

    console.log(data)

    //this.toastr.success('Hello world!', 'Toastr fun!');
    this.addr_service.add(data).subscribe(data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if(data.error.status){
        this.loadingForm.next(false)
        return
      }
      if (data.address) {
        this.toastr.success('Item Added!');
        this.router.navigate(['../'], { relativeTo: this.route });
      } else {
        this.toastr.error('Unknown server error');
      }
      this.loadingForm.next(false)
    })
  }

}
