import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
//Toastr
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'kt-obj-addresses-form',
  templateUrl: './obj-addresses-form.component.html',
  styleUrls: ['./obj-addresses-form.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ObjAddressesFormComponent implements OnInit {

  @Input() options

  @Input() validation
  @Input() loading

  @Input() preload


  constructor(private toastr:ToastrService) { }

  ngOnInit() {
    this.validation.subscribe(data => {
      //console.log(data)
      for (let key in data) {
        if (data[key]) {
          //console.log(data[key])
          for (let i = 0; i < data[key].length; i++) {
              this.toastr.error(data[key][i]);
          }
        }
      }
    })
  }

}
