import { Component, OnInit } from '@angular/core';
//Router
import { Router, ActivatedRoute } from '@angular/router';
//RXJS
import { BehaviorSubject } from 'rxjs';
import { first } from 'rxjs/operators';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Model Address
import { Address } from '../../../../../partials/layout/tacgui/_forms/obj-addresses-form/obj-address.model'
//Service//
import { AddressesService } from '../addresses.service'

@Component({
  selector: 'kt-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

  validation = new BehaviorSubject({})
  loadingForm = new BehaviorSubject(false)

  formParameters = {
    action: 'add',
    data: new Address,
    old_data: new Address
  }

  constructor(
    private toastr:ToastrService,
    private addr_service: AddressesService,
    private router:Router,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.formParameters.data.empty()
    this.formParameters.old_data.empty()
    this.route.paramMap.pipe(
      first()
    ).subscribe( params => {
      this.fillForm(+params.get('id'))
    });
  }

  fillForm(id:number){
    // console.log(id)
    this.addr_service.get(id).subscribe( data => {
      // console.log(data)
      if (!data){
        this.toastr.error('Server Error!')
        this.router.navigate(['../../'], { relativeTo: this.route });
        return
      }
      Object.assign(this.formParameters.data, data)
      Object.assign(this.formParameters.old_data, data)
      this.loadingForm.next(false)
    })
  }

  save(){
    this.loadingForm.next(true)
    if (JSON.stringify(this.formParameters.data) == JSON.stringify(this.formParameters.old_data)){
      this.toastr.warning('No Changes Detected!')
      this.loadingForm.next(false)
      return;
    }
    this.addr_service.save(this.formParameters.data).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loadingForm.next(false)
        return
      }
      if (data.save) {
        this.toastr.success('Item Changed!');
        this.router.navigate(['../../'], { relativeTo: this.route });
      } else {
        this.toastr.error('Unknown server error');
      }
      this.loadingForm.next(false)
    })
  }

}
